import { Component, OnInit, AfterViewInit, ViewChild, TemplateRef } from '@angular/core';
import { FileInfo } from '../../models/file';
import { GitDataService } from '../../services/git-data.service';
import { FILE_INFO } from '../../constants/mockdata';
import { BsModalService, BsModalRef, MessagerService, NotifyService } from '@farris/ui';
import { GitPushDialogComponent } from '../git-dialog/push-dialog/git-push-dialog.component';
import { GitPanelState } from './git-panel.state';
import { GitRemoteAddDialogComponent } from '../git-dialog/remote-add-dialog/git-remote-add-dialog.component';
import { GitReleaseBranchDialogComponent } from '../git-dialog/release-branch-dialog/git-release-branch-dialog.component';
import { GitPullDialogComponent } from '../git-dialog/pull-dialog/git-pull-dialog.component';

@Component({
  selector: 'app-git-panel',
  templateUrl: './git-panel.component.html',
  styleUrls: ['./git-panel.component.css'],
  providers: [
    BsModalService
  ]
})
export class GitPanelComponent implements OnInit, AfterViewInit {

  gitProjectPath = '';
  gitBranchPath = '';
  focusedNodePathInfo = '';
  needInitGitReposity = true;

  files: FileInfo[] = []; // git

  comment = '';
  message = '';

  alerts = [];

  mock = false; // mockfalse()

  gitRemoteAddWarning = '';

  @ViewChild('btnPushOkRef') btnPushOk: TemplateRef<any>; // 
  dlgGitPush: BsModalRef; // git push

  @ViewChild('btnPullOkRef') btnPullOk: TemplateRef<any>; // 
  dlgGitPull: BsModalRef; // git pull

  @ViewChild('btnRemoteAddRef') btnRemoteAdd: TemplateRef<any>; // 
  dlgGitRemoteAdd: BsModalRef; // 

  @ViewChild('btnReleaseBranchRef') btnReleaseBranch: TemplateRef<any>; // 
  dlgGitReleaseBranch: BsModalRef; // 

  constructor(public gitDataService: GitDataService, private modalService: BsModalService,
    private msgService: MessagerService, private notifyService: NotifyService, private gitPanelState: GitPanelState) {

  }

  ngOnInit() { }

  public setGitBranchPath(gitBranchPath: string) {
    this.gitBranchPath = gitBranchPath;
  }

  public setFocusedNodePathInfo(focusedNodePath: string) {
    this.focusedNodePathInfo = focusedNodePath;
  }

  // GitneedInitGitReposity Flag
  private setNeedInitGitReposity() {
    if (this.gitProjectPath && this.gitProjectPath.length > 0) {
      this.needInitGitReposity = false;
    } else {
      this.needInitGitReposity = true;
    }
  }
  public setGitProjectPath(relativeProjectPath: string) {
    console.log('Git project path is:' + relativeProjectPath);
    this.gitProjectPath = relativeProjectPath;

    this.setNeedInitGitReposity();

    // gitProjectPath
    this.fetchFilesFromServer();
  }


  ngAfterViewInit(): void {
    // Gitthis.files
    this.fetchFilesFromServer();
  }

  // fileListeventpanelthis.files
  getData(event) {
    this.files = event;
    console.log(this.files);
  }

  /**
   * panel
   * 
   */
  commit() {
    //  git commit -m "xxx"
    // inputmessage
    if (this.comment === '') {
      // console.log('Please input some message when committing...');
      this.alerts.push({
        type: 'warning',
        msg: `Please input some message when committing...`,
        timeout: 3000
      });
      return false;
    }
    this.message = this.comment;
    // API: git commit -m "xxx"
    this.gitDataService.postGitCommit(this.gitProjectPath, this.message).subscribe(
      () => {
        console.log('Git commit -m' + this.message);
        // this.message
        this.message = '';
        this.comment = '';
        this.fetchFilesFromServer();
      }
    );
  }

  // (Ctrl+Entercommit)
  addCommentThenCommit() {
    this.message = this.comment;
    // commit APIstagedcommit
    console.log('Git commit -m ' + this.message);
    this.comment = '';
  }

  // ()
  asyncToGit(): void {
    // API
    console.log('Async to git server...');
  }

  /**
   * 
   * API: git pull
   */
  gitPull(): void {
    this.showPullDialog();
  }

  /**
   * post
   */
  postPull() {
    this.dlgGitPull.close();

    const userName = this.dlgGitPull.content.GitPullName;
    const password = this.dlgGitPull.content.GitPullPassword;
    if (null === userName || undefined === userName
      || null === password || undefined === password) {
      this.msgService.warning('pull');
      return;
    }

    // 
    this._getGitRemoteRepoisty(
      (remoteReposity) => {
        this._postPull(this.gitProjectPath, userName, password, remoteReposity);
      },
      () => {
        this.msgService.error(this.gitRemoteAddWarning);
      }
    );
  }

  // /**
  //  * post
  //  */
  // postPullRebase() {
  //   this.dlgGitPullRebase.close();

  //   const userName = this.dlgGitPull.content.GitPullName;
  //   const password = this.dlgGitPull.content.GitPullPassword;
  //   if (null === userName || undefined === userName
  //     || null === password || undefined === password) {
  //     this.msgService.warning('pull');
  //     return;
  //   }

  //   // 
  //   this._getGitRemoteRepoisty(
  //     (remoteReposity) => {
  //       this._postPull(this.gitProjectPath, userName, password, remoteReposity);
  //     },
  //     () => {
  //       this.msgService.error(this.gitRemoteAddWarning);
  //     }
  //   );
  // }

  /**
   * 
   * API: git pull --rebase origin master
   */
  gitPullWithRebase() {
    // this.showPullRebaseDialog();
  }

  /**
   * post 
   * @param gitProjectPath git
   * @param userName 
   * @param password 
   * @param remoteReposity 
   * @param isRebase 
   */
  _postPull(gitProjectPath: string, userName: string, password: string, remoteReposity: string, isRebase: boolean= false) {
    this.gitDataService.postGitPull(gitProjectPath, userName, password,
      remoteReposity, isRebase).subscribe(
      pullHttpResponse => {
        if (200 === pullHttpResponse.status) {
          this.notifyService.success('');
          // git status -> fetch data from server -> update file list panel
          this.fetchFilesFromServer();
        } else {
          this.msgService.error('' + pullHttpResponse.Message);
        }
      },
      pullHttpErrorResponse => {
        this.msgService.error('' + pullHttpErrorResponse.error.Message);
      }
    );
  }

  /**
   * 
   * API: git push
   */
  gitPush(): void {
    this.showPushDialog();
  }

  /**
   * 
   * api: git remote -v
   * @param successCallback 
   * @param errorCallback 
   */
  _getGitRemoteRepoisty(successCallback: Function, errorCallback: Function) {
    this.gitDataService.getGitRemote(this.gitProjectPath, null, true).subscribe(
      httpResponse => {
        if (null != httpResponse.body && httpResponse.body.length > 0) {// 
          const currentRemote = httpResponse.body[0];
          const remoteReposityName = currentRemote.name;
          if (null != remoteReposityName && remoteReposityName.length > 0) {// 
            successCallback(remoteReposityName);
          } else {// 
            errorCallback();
          }
        } else { // 
          this.msgService.warning(this.gitRemoteAddWarning);
          return;
        }
      },
      httpErrorResponse => { // 
        this.msgService.error('' + httpErrorResponse.error.Message);
      }
    );
  }

  /**
   * post
   */
  postPush() {
    this.dlgGitPush.close();

    const branchName = this.gitBranchPath;
    if (null === branchName || undefined === branchName) {
      this.msgService.warning('push');
      return;
    }

    const userName = this.dlgGitPush.content.GitPushName;
    const password = this.dlgGitPush.content.GitPushPassword;
    if (null === userName || undefined === userName
      || null === password || undefined === password) {
      this.msgService.warning('push');
      return;
    }

    // 
    this._getGitRemoteRepoisty(
      (remoteReposity) => {
        this.gitDataService.postGitPush(this.gitProjectPath, remoteReposity, branchName, userName, password).subscribe(
          httpResponse => {
            if (httpResponse.status === 200) {
              this.notifyService.success('');
              // git status -> fetch data from server -> update file list panel
              this.fetchFilesFromServer();
            }else {
              this.msgService.warning('');
            }
          },
          httpErrorResponse => {
            this.msgService.error('Git Push ' + httpErrorResponse.error.Message);
          }
        );
      },
      () => {
        this.msgService.error(this.gitRemoteAddWarning);
      }
    );
  }

  /**
   * 
   * API: git push -u origin master
   */
  gitReleaseBranch() {
    this.showReleaseBranchDialog();
  }

  /**
   * post
   */
  postReleaseBranch() {
    this.dlgGitReleaseBranch.close();

    // 
    const currentBranch = this.gitBranchPath;
    if (null === currentBranch || undefined === currentBranch) {
      this.msgService.warning('');
      return;
    }

    const userName = this.dlgGitReleaseBranch.content.UserName;
    const password = this.dlgGitReleaseBranch.content.Password;
    if (null === userName || undefined === userName
      || null === password || undefined === password) {
      this.msgService.warning('push');
      return;
    }

    // 
    this._getGitRemoteRepoisty(
      (remoteReposity) => {// success
        this.gitDataService.postReleaseBranch(this.gitProjectPath, remoteReposity,
          currentBranch, userName, password).subscribe(
            httpResponse => {
              if (httpResponse.status === 200) {
                this.notifyService.success('');
              } else {
                this.msgService.warning('');
              }
            },
            httpErrorResponse => {
              this.msgService.error('' + httpErrorResponse.error.Message);
            }
          );
      },
      () => {// error
        this.msgService.error(this.gitRemoteAddWarning);
      }
    );
  }

  /**
   * 
   */
  gitRemoteAdd() {
    this.showRemoteAddDialog();
  }

  /**
   * post
   */
  postGitRemoteAdd() {
    this.dlgGitRemoteAdd.close();

    const name = this.dlgGitRemoteAdd.content.RemoteReposityName;
    if (undefined === name || null === name || name.length <= 0) {
      this.msgService.warning('name');
      return;
    }

    const remoteUrl = this.dlgGitRemoteAdd.content.RemoteReposityUrl;
    if (undefined === remoteUrl || null === remoteUrl || remoteUrl.length <= 0) {
      this.msgService.warning('url');
      return;
    }
    // todourl

    // 
    this.gitDataService.postGitRemoteAdd(this.gitProjectPath, name, remoteUrl).subscribe(
      remoteAddHttpResponse => {
        if (200 === remoteAddHttpResponse.status) {
          this.notifyService.success('');
        } else {
          this.msgService.error('');
        }
      },
      httpErrorResponse => {
        this.msgService.error('git remote add' + httpErrorResponse.error.Message);
      }
    );
  }

  /**
   * 
   */
  showReleaseBranchDialog() {
    this.dlgGitReleaseBranch = this.modalService.show(GitReleaseBranchDialogComponent, {
      title: '',
      width: 350, height: 250,
      buttons: this.btnReleaseBranch,
      showButtons: true,
      showMaxButton: false,
      resizable: false
    });
  }

  /**
   * 
   */
  showRemoteAddDialog() {
    this.dlgGitRemoteAdd = this.modalService.show(GitRemoteAddDialogComponent, {
      title: '',
      width: 350, height: 250,
      buttons: this.btnRemoteAdd,
      showButtons: true,
      showMaxButton: false,
      resizable: false
    });
  }

  /**
   * 
   */
  showPullDialog() {
    this.dlgGitPull = this.modalService.show(GitPullDialogComponent, {
      title: 'Git Pull',
      width: 400, height: 300,
      buttons: this.btnPullOk,
      showButtons: true,
      showMaxButton: false,
      resizable: false
    });
  }

  /**
   * 
   */
  showPushDialog() {
    this.dlgGitPush = this.modalService.show(GitPushDialogComponent, {
      title: 'Git Push',
      width: 400, height: 300,
      buttons: this.btnPushOk,
      showButtons: true,
      showMaxButton: false,
      resizable: false
    });
  }

  // 
  commitAllFiles(): void {
    console.log('git add .');
    this.commitStagedFiles();
    // git status -> fetch data from server -> update file list panel
  }

  /**
   * 
   * API: git commit -m "xxx"
   */
  commitStagedFiles(): void {
    console.log('Committing staged files to server...');
    this.commit();
  }

  /**
   * 
   */
  resetLastCommittedFiles(): void {
    // API: git reset HEAD~
    console.log('Resetting last committed files...');
    this.gitDataService.cancelLastGitCommit(this.gitProjectPath).subscribe(
      () => {
        console.log('git reset HEAD~');
        this.fetchFilesFromServer();
      }
    );
  }

  // 
  stageAllFiles(): void {
    // API: git add .
    console.log('Staging all files...');
    // git status -> fetch data from server -> update file list panel
    this.fetchFilesFromServer();
  }

  // 
  removeAllStagedFiles(): void {
    // API: git reset HEAD .
    console.log('Removing all staged files...');
    // git status -> fetch data from server -> update file list panel
    this.fetchFilesFromServer();
  }

  /**
   * API: 
   * @example this.files = newFileArray
   */
  public fetchFilesFromServer() {
    if (this.mock) {
      this.fetchFilesByMock();
      return;
    }

    if (!this.gitProjectPath || this.gitProjectPath.length <= 0) {
      console.log('When fetching Files From the Server, project path is  null');
      return;
    }

    const self = this;
    self.gitDataService.getGitStatusData(self.gitProjectPath).subscribe((data) => {
      self.files = data;
      console.log(' Already got file list from server');
      console.log(self.files);
    });
    console.log('Getting file list from server...');
  }

  /**
   * mockfetch Files Data
   */
  private fetchFilesByMock() {
    const fileInfoArray = FILE_INFO;
    this.files = [];
    fileInfoArray.forEach(element => {
      const tempFileInfo = new FileInfo(element);
      this.files.push(tempFileInfo);
    });
  }

  /**
   * 
   */
  refresh() {
    // 
    this.fetchFilesFromServer();
  }

  /**
   * 
   */
  initGitReposity() {
    const self = this;
    self.gitDataService.postGitInit(self.focusedNodePathInfo).subscribe(
      (data) => {
        console.log('Init git reposity done');
        //  git init
        self.gitProjectPath = self.focusedNodePathInfo;
        this.setNeedInitGitReposity();
        // git init
        this.gitPanelState.gitInit.emit(this);

        self.fetchFilesFromServer();
      },
      error => {
        console.log('Git init observable object is null');
      }
    );
  }
}



