import { Component, OnInit, OnChanges, Input, Output, EventEmitter } from '@angular/core';
import { FileInfo } from '../../models/file';
import { GitDataService } from '../../services/git-data.service';
import { Observable } from 'rxjs';

const FILE_PATH_SEPARATOR = '/';

@Component({
  selector: 'app-git-filelist',
  templateUrl: './git-filelist.component.html',
  styleUrls: ['./git-filelist.component.css']
})
export class GitFilelistComponent implements OnInit, OnChanges {

  @Input() gitProjectPath: string;
  @Input() fileList: FileInfo[];

  updatedFileList: FileInfo[]; // 

  untrackedFiles: FileInfo[];
  trackedFiles: FileInfo[];

  stagedFiles: FileInfo[];
  unStagedFiles: FileInfo[];

  otherFiles: FileInfo[];

  @Output() event = new EventEmitter();



  constructor(public gitDataService: GitDataService) { }

  ngOnInit() { }


  ngOnChanges() {
    console.log(this.fileList);
    const allFiles = this.fileList || [];

    const untrackedFiles = allFiles.filter((file, index) => {
      return file.fileTracked === false;
    });

    const trackedFiles = allFiles.filter((file, index) => {
      return file.fileTracked === true;
    });

    const stagedFiles = allFiles.filter((file, index) => {
      return file.fileStaged === true;
    });

    const unStagedFiles = allFiles.filter((file, index) => {
      return file.fileStaged === false;
    });

    let otherFiles = unStagedFiles.concat(untrackedFiles);
    otherFiles = Array.from(new Set(otherFiles));

    this.unStagedFiles = unStagedFiles;

    this.trackedFiles = trackedFiles;
    this.untrackedFiles = untrackedFiles;

    this.stagedFiles = stagedFiles; // 
    this.otherFiles = otherFiles; // staged
  }

  // +-API
  fetchFilesFromServer() {
    console.log('updating files...');
    // TODO: API
    // this.updatedFileList = this.fileList;
    const self = this;
    self.gitDataService.getGitStatusData(self.gitProjectPath).subscribe((gitStatusData) => {
      self.updatedFileList = gitStatusData;
      // this.updatedFileList = this.fileList.concat(
      //   {fileName: Math.ceil(Math.random() * 10) * Math.ceil(Math.random() * 10) +
      //     'fileheader.js', filePath: '/git-header/', fileStatus: 'M', fileStaged: true, fileTracked: true}
      //   );
      // emitgit-panel,git-panelgit-panel
      // (git-panelemitAPI)
      self.event.emit(this.updatedFileList);
    });

  }

  /**
 * +
 * APIgit add filename
 * @param file
 * @param nextFunction
 * @param errorFunction
 */
  gitAdd(file): Observable<FileInfo> {
    // APIgit add filename

    const fileRelativePath: string = this._getFileRelativePath(file);
    console.log('git add ' + fileRelativePath);
    return this.gitDataService.postGitAdd(this.gitProjectPath, fileRelativePath);
  }

  /**
   * 
   * APIgit checkout -- filename
   * @param file
   * @param nextFunction
   * @param errorFunction
   */
  gitDiscard(file): Observable<FileInfo> {
    // APIgit checkout -- filename
    console.log('git checkout -- ' + file.filePath + file.fileName);
    const fileNameWithPath: string = this._getFileRelativePath(file);
    return this.gitDataService.discardAddedFile(this.gitProjectPath, fileNameWithPath);
  }

  private _getFileRelativePath(file): string {
    let fileRelativePath = '';

    if (file.filePath.length <= 0) {
      fileRelativePath = file.fileName;
    } else {
      fileRelativePath = file.filePath + FILE_PATH_SEPARATOR + file.fileName;
    }
    return fileRelativePath;
  }

  /**
   * -
   * APIgit reset HEAD fileName
   */
  gitRemove(file): Observable<FileInfo> {
    // APIgit reset HEAD fileName
    const fileRelativePath: string = this._getFileRelativePath(file);
    console.log('git reset HEAD ' + fileRelativePath);
    return this.gitDataService.cancelStagedFile(this.gitProjectPath, fileRelativePath);
  }

  // 
  onDiscardFile(file): void {
    this.gitDiscard(file).subscribe(
      next => {
        this.fetchFilesFromServer();
      }
    );
  }

  /**
   * item+git add
   * @param file
   */
  onAddFile(file): void {
    // mock
    // this.otherFiles.splice(this.otherFiles.indexOf(file), 1);
    // const addedFile = file;
    // addedFile.fileStaged = true;
    // addedFile.fileTracked = true;
    // this.stagedFiles.push(addedFile);
    // 
    this.gitAdd(file).subscribe(
      next => {
        this.fetchFilesFromServer();
      }
    );
  }

  /**
   * item-git add
   * @param file
   */
  onRemoveFile(file): void {
    // mock
    // this.stagedFiles.splice(this.stagedFiles.indexOf(file), 1);
    // const removedFile = file;
    // removedFile.fileStaged = false;
    // removedFile.fileTracked = false;
    // this.otherFiles.push(removedFile);
    // 
    this.gitRemove(file).subscribe(
      next => {
        this.fetchFilesFromServer();
      }
    );
  }

  stageAllFiles(): void {
    // git add -A -- .
    console.log('git add -A -- .');
    this.fetchFilesFromServer();
  }

  removeAllStagedFiles(): void {
    // git reset -q HEAD -- .
    console.log('git reset -q HEAD -- .');
    this.fetchFilesFromServer();
  }

  discardAllEdit() {
    // git checkout -- .
    console.log('git checkout -- .');
    this.fetchFilesFromServer();
  }
}



